.location-table {
	width: 100%;
	//margin:0 auto;
	font-size: 0.75rem;

	@include breakpoint-up(medium) {
		font-size: 1.13rem;
	}

	margin-bottom:6rem;

	td {
		//vertical-align: top;
		padding-top: 0.5rem;
		padding-bottom: 0.5rem;
		padding: 0.5rem 1rem;

		@include breakpoint-down(small) {
			&:first-child {
				display: none;
			}
		}

	}

	tr:nth-child(even) {
		background-color: rgba(0, 0, 0, 0.03);
	}

	.header-row {
		text-transform: uppercase;
	}

	.location-row {
		td:nth-child(2) {
			font-family: "MrEavesXLModOT-Bold";
			text-transform: uppercase;
			letter-spacing: 0.025rem;
		}
	}

	.location-website {
		display: block;
		text-decoration: underline;
		cursor: pointer;
	}

	.location-phone {
		display: block;
	}
}

.taxi-entries {
	max-width: 100%;
	align-items: flex-start;
	flex-wrap: wrap;

	.entry {
		display: flex;
		gap: 1rem;
		margin-bottom: 1rem;
		align-items: center;

		.title {
			font-family: "MrEavesXLModOT-Bold";
			text-transform: uppercase;
			letter-spacing: 0.025rem;
			flex: 1 1 100%;

		}

		.phone-number {
			flex: 1 1 100%;
			text-align: right;
		}
	}

	h2 {
		text-align: left;
		font-weight: 500;
		text-transform: none;
		font-size: 1.25rem;
		letter-spacing: 0;
	}

	.col {
		width: 100%;
		//flex: 0 1 33%;

		// 	@include breakpoint-up(large) {
		// 		flex: 1 0 45%;
		// 	}
	}
}