@import url('https://fonts.googleapis.com/css2?family=Corinthia&display=swap');

.nav-wrapper {
	background-color:$midgrey;
	margin-top:0;
}

nav {


	text-transform: uppercase;
	font-weight: 300;
	font-size: 0.9rem;
	color: $midgrey;
	letter-spacing: 0.5rem;
	line-height: 2;
	text-align: center;

	color: $white;
	padding:1rem;

	padding-bottom:4rem;


	@include breakpoint-up(medium) {
		font-size: 1.3rem;
	}
	@include breakpoint-up(large) {
		padding-bottom:1rem;
	}
	

	.icon {
		fill: white;
		max-width: 60px;
		max-height: 60px;
		padding: 1.2rem;
		
	}

	a {
		color: $lightergrey !important;
	}

	ul {
		display: flex;
		flex-direction: column;
		
		@include breakpoint-up(large) {
			flex-direction: row;
		}
		gap: 0.5rem;
		@include breakpoint-up(medium) {
			gap: 1rem;
		}
		@include breakpoint-up(large) {
			gap: 3rem;
		}
		justify-content: center;
		align-items: center;
		width: 100%;

		li {
			display: inline;
			margin: 0;
			padding:0 0.15rem;

			border-bottom: 3px solid rgba(255, 255, 255, 0);
			transition-duration: 0.25s;

			a:has(.icon){
				display:block;
				line-height:0;
			}

			&:has(.icon){
				padding:0;
			}

			&:hover {
				color: $white;
				border-bottom: 3px solid white;
			}


		}

		.special {
			background-color: $white;
			padding: 0.1rem 1rem;
			border-radius: .5rem;
			font-weight: 800;

			border: 3px solid white;
			cursor: pointer;

			a {
				color: $midgrey !important;
			}

			&:hover {
				border: 3px solid white;
				background-color: rgba(255, 255, 255, 0.3);

				a {
					color: $white !important;
				}
			}
		}
	}
}