html {
	font-size: 100%;
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	margin: 0;
	padding: 0;
	background-color: $white;

}

.container {
	max-width: 1400px;
	margin: 0 auto;
	padding-top: 4rem;
	padding-bottom: 4rem;
}

ul {
	list-style-type: square;
	padding-inline-start: 0px;
	margin: 0;

	li {
		margin-top: 0;
		margin-bottom: 2rem;

		&:last-child {
			margin-bottom: 0;
		}
	}

	li h2 {
		margin-bottom: 0;
	}
}

hr {
	width: 20%;
	height: 8px;
	margin-top: 4rem;
	margin-bottom: 4rem;
	border: 0;
	border-bottom: 8px solid $midgrey;
}

img {
	padding: 0;
	margin: 0;
	display: block;
	max-width: 100%;
}

video {
	max-width: 100%;
	margin: 0 auto;
}

.video-wrap {
	display: flex;
	max-height: 100%;
}

.icons {
	display: none;
}

.focus-green {
	color: $white;
}

section:has(.focus-green) {
	background-color:#4d5851;
	@include breakpoint-up(large) {
		background: linear-gradient(to right, #4d5851 53%, white 0%);
	}
	
}

section:has(.focus-grey) {
	background-color:#ddd;
	@include breakpoint-up(large) {
		background: linear-gradient(to left, #ddd 53%, white 0%);
	}
	
}