@import url('https://fonts.googleapis.com/css2?family=Amatic+SC:wght@400;700&display=swap');

*{
	font-family: "Electra LT Std", serif;
	
}

body {
	line-height: 1.4;
	font-weight: 400;
	color: $midgrey;
}

a,
a:hover,
a:visited,
a:active {
	text-decoration: none;
	color: $darkgrey;
	cursor: pointer;
	transition-timing-function: ease-in-out;
	transition-duration: 0.1s;
}

a:hover {

	color: $darkergrey;
	cursor: pointer;
}

h1, h2, h3, h4, h5, h6{
    margin:0;	
}

h1, h2 {
	text-transform:lowercase;
	font-family: "Electra LT Std", serif;
}

h1 {
	font-size: 3.5rem;
	@include breakpoint-up(small) {
		font-size: 5.5rem;
	}
	line-height:5rem;
	font-weight:100;
	margin-bottom: 4.5rem;	

}

h2 {
	text-align:center;
	font-size: 2.2rem;
	letter-spacing: 0.25rem;
	color: $midgrey;
	margin-bottom: 0.75rem;
}

h3 {
	text-align:center;
	font-size:1.75rem;
	font-weight:200;
}

i {
	margin-right:0.1rem;
}

p {
    margin-top:0;
	font-size:1.2rem;
}


strong {
    font-weight:700;
	
   // color: $white;
}

blockquote {
    padding:1rem 0;
    margin:0;
    margin-bottom:0.75rem;
	font-size:150%;
    >p:last-child {
        margin-bottom:0;
    }
}

blockquote.large {
	margin-top:1rem;
	margin-bottom:2rem;
	padding:2rem;
	font-size:1.2rem;
	line-height: 2;
}