@import url('https://fonts.googleapis.com/css2?family=Corinthia&display=swap');

.cover-image {
	background-image: url("../img/Tythe_Header.jpg");
	background-size: cover;
	background-position: center;
}

.header {

	text-align: center;
	font-size: 1.7rem;
	line-height: 1.6;
	font-weight: 400;
	padding:0;
	
	padding-left: 1rem;
	padding-right: 1rem;
	@include breakpoint-up(medium) {
		padding-left: 4rem;
		padding-right: 4rem;
	}

	display: flex;
	align-items: center;

	min-height:100vh;

	color: $white;

	h1 {
		font-size: 6rem;
		line-height:6rem;

		@include breakpoint-up(medium) {
			font-size: 14rem;
			line-height:14rem;
		}

		
		font-family: 'Corinthia', cursive;
		margin-bottom:0;
		text-transform: none;
	}

	h2 {
		font-size: 1.5rem;
		@include breakpoint-up(medium) {
			font-size: 2.5rem;
		}

		color: white;
		
		display: inline-block;
		margin-bottom: 0;
		
	font-family: 'Amatic SC', serif;
	}

	.icon {
		fill: white;
		max-width: 60px;
		max-height: 60px;
		padding: 1.2rem;
	}

	span {
		display: flex;
		align-items: center;
		justify-content: center;
	}

	span::before {
		content:"";
		flex:2 1 10px;
		
		margin-right:2rem;
		border-bottom:2px solid rgba(255,255,255,0.5);
	}
	span::after {
		content:"";
		flex:2 1 10px;
		
		margin-left:2rem;
		border-bottom:2px solid rgba(255,255,255,0.5);
	}
}