.login-box {
	display: flex;
	align-content: center;
	justify-content: center;
	align-items: center;

	>* {
		flex: 1 1 600px;
		background-color: #f7f8f9;
		padding: 4rem;
	}

	h1 {
		font-size: 5rem;
		color: #666;
		text-transform: none;
	}

	input:-webkit-autofill {
		-webkit-box-shadow:0 0 0 50px #f7f8f9 inset; /* Change the color to your own background color */
		-webkit-text-fill-color: #333;
	}
	
	input:-webkit-autofill:focus {
		-webkit-box-shadow: 0 0 0 50px #f7f8f9 inset;/*your box-shadow*/
		-webkit-text-fill-color: #333;
	} 
}

.thankyou-box {
	background-color: #f7f8f9;
	padding: 4rem;


	h1 {
		font-size: 5rem;
		color: #666;
		text-transform: none;
	}
}

.button-row {
	display: flex;
	flex-direction: row;
	gap: 1rem;

	>* {
		flex: 1 1 0;
		padding: 1rem;
		font-size: 1.5rem;
		border: 0;

		color: #fff;
		cursor:pointer;
	}

	.color-no {
		background-color: #bbb;
	}

	.color-yes {
		background-color: rgb(124, 158, 212);
	}
}

.login-box h2 {
	margin: 0 0 30px;
	padding: 0;
	text-align: center;
}

.login-box .user-box {
	position: relative;
}

.login-box .user-box input,
.login-box .user-box textarea {
	width: 100%;
	padding: 10px 0;
	font-size: 1.5rem;
	margin-bottom: 30px;
	border: none;
	border-bottom: 1px solid #aaa;
	outline: none;
	background: transparent;
}

.login-box .user-box label {
	position: absolute;
	top: 0;
	left: 0;
	padding: 10px 0;
	font-size: 1rem;
	pointer-events: none;
	transition: .25s;
	color: #888;
}

.login-box .user-box input:focus~label,
.login-box .user-box input:not(:placeholder-shown)~label,
.login-box .user-box textarea:focus~label,
.login-box .user-box textarea:not(:placeholder-shown)~label {
	top: -20px;
	left: 0;
	color: #aaa;
	font-size: 12px;
}

